<template>
    <div class="good_left">
        
        <van-popup v-model="show" position="bottom" :style="{ height: '40%' }">
            <div class="ding">
                    <div></div>
                    <div @click="del"><van-icon name="delete"/></div>
                </div>
            <div v-for="(item,key) in info" :key="key" class="watch">
                
                <span>{{item.name}}</span>
                <span>{{item.price}}</span>
                <span>
                    <input type="button" value='-' @click="cutnum(key)">
                    <input type="text" :value='item.number'>
                    <input type="button" value="+" @click="addnum(key)">
                </span>
            </div>
        </van-popup>
        <div class="box">
        <!--  -->
        <div class="box_left">
            <!-- 455555 -->
            <div v-for='(v,i) in list' :class="{'active':i==num}" :key="i" @click='scoll(i)'>{{v.name}}</div>
        </div>
        <!--  -->


        <!--  -->
        <div class="box_right" >
            <ul v-for='(v,i) in list' :key="i" ref="box">
                <li class="nav">
                    <h2>{{v.name}}</h2>
                    <div>{{v.description}}</div>
                </li>
                <!--  -->
                <li class="content" v-for='(item,key) in v.foods' :key="key">
                    <div class="image">
                        <img :src="'https://elm.cangdu.org/img/'+item.image_path" />
                    </div>
                    <div class="imgright">
                        <h2>{{item.name}}</h2>
                        <div class="ir1">{{item.description}}</div>
                        <div class="ir2">{{item.month_sales}} {{item.satisfy_rate}}%</div>
                        <!-- <div v-if='item.activity.image_text!=null'>{{item.activity.image_text}}</div> -->
                        <!-- <div v-else></div> -->
                        <div class="ir3">{{item.specfoods[0].price}}<span></span></div>
                    </div>
                    <div class="add">
                            <button @click="addCart(item)" class="add1">+</button>
                    </div>        
                            
                        
                </li>
                <!--  -->
            </ul>
        </div>
        <!--  -->
        </div>
        <div class="button">
            <van-button type="primary" @click="showPopup" v-if="this.$store.state.gou.length>=1">
            <div class="div-a"><van-icon name="cart-o" :info='znum()'/></div>
            <div class="div1">
                <div>{{zong()}}</div>
                <div>5</div>
            </div>
            </van-button>
            

            <van-button type="primary" v-else>
            <div class="div-a"><van-icon name="cart-o" :info='znum()'/></div>
            <div class="div1">
                <div>{{zong()}}</div>
                <div>5</div>
            </div>
            </van-button>
            <div class="div2" v-if="song>=20"></div>
            <div class="div3" v-else>{{20-zong()}}</div>
            
        </div>
        
    </div>
</template>


<script>
import jq from 'jquery'   
import axios from 'axios'    
export default{
    name:'Left',
    data() {
        return {
            list:[], // 
            num:0,
            HeightTop:[],
            info:[],
            show:false,
            song:0
        }
    },
    mounted(){
        // 
        console.log('aa')
        let id=this.$route.query.id
        axios.get('https://elm.cangdu.org/shopping/v2/menu?restaurant_id='+id).then((res)=>{
            // console.log('aaa')
            console.log(res)
            this.list = res.data
            this.$nextTick(()=>{
                this.height()
            })
        })
        this.info=this.$store.state.gou
        console.log(this.info)
    },
    methods: {
        //
        zong(){
            let arr = this.$store.state.gou
            let price = 0
            for(let i in arr){
                price += arr[i].price * arr[i].number
            }
            this.song = price
            return price
        },

        // 
        znum(){
            let arr = this.$store.state.gou
            let num = 0
            for(let i in arr){
                num += arr[i].number
            }
            return num
        },

        showPopup(){
            this.show=true
           
            
        },
        //
        addnum(key){
            this.$store.commit('add1',key)
        },
        //
        cutnum(key){
            this.$store.commit('cut1',key)
        },
        //
        del(){
            this.$store.commit('del')
        },
        //
        scoll(i){
            this.num = i
            // console.log(jq('.box_right').animate)
            jq('.box_right').animate({scrollTop:this.HeightTop[i]})


        },
        height(){
            // 
            var box = this.$refs.box;
            // console.log(box)
            // 
            var height = 0
            // 
            this.HeightTop.push(height)
            // 
            for(let i in box){
                // 
                height+=box[i].clientHeight
                // 
                this.HeightTop.push(height)
            }
            // console.log(this.HeightTop)
        },
        //
        addCart(item){
            console.log(item)
            var obj={id:item.specfoods[0]._id,name:item.specfoods[0].name,price:item.specfoods[0].price,number:1}
            
            var arr=this.$store.state.gou
            var index=arr.findIndex((v,i,arr)=>{
                return v.id==item.specfoods[0]._id
            })
            //
            if(index==-1){
                this.$store.commit('gou',obj)
            }else{
                this.$store.commit('addnum',index)
            }
        }
        
    }
}
</script>


<style lang="scss" scoped>
.div2{ width: 30%;
        height: 100%;
        color: white;
        float: right;
        text-align: center;
        font-weight: bold;
        line-height: 1.1rem;
        background: green;
}
.van-popup{
    margin-bottom: 1.1rem;
}
h2{
    width: 2rem;
    padding: .12rem 0 ;
    font-size: .32rem;
    padding: 0;
    margin: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

}
.good_left{
    margin-top: 1rem;
    font-size: .32rem;
    width: 100%;
    height: 100%;
    .box{
        width: 100%;
        height: 100%;
        position: relative;
        margin-bottom: 1.1rem;
        .box_left{
            width: 20%;
            height:100%;
            overflow-y: auto;
            position: absolute;
            left: 0;
            top: 0;
            overflow: hidden;
            overflow: scroll;
            div{
                width: 100%;
                height: .8rem;
                border-top: 0.01rem solid #f5f5f5;
                text-align: center;
                line-height: .8rem;
                overflow: hidden;
                text-overflow:ellipsis;
                white-space: nowrap;
            }
            .active{
                background: white;
            }
        }
        .box_right{
            overflow-y: auto;
            width: 80%;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
            background: white;
            ul{
                width: 100%;
                .nav{
                    width: 100%;
                    height: 0.6rem;
                    background: #F5F5F5;
                    display: flex;
                    line-height: 0.6rem;
                    // padding-left: 0.1rem;
                    div{
                        margin-top:0.05rem;
                        // margin-left:0.1rem;
                    }
                }
                .content{
                    width: 100%;
                    height: 2.6rem;
                    border-bottom: 1px solid #F5F5F5;
                    display: flex;
                    .image{
                        width: 25%;
                        height:100%;
                        // padding:0 0.25rem;
                        img{
                            width: 1rem;
                            height: 1rem;
                            vertical-align: middle;
                        }
                    }
                }
                .imgright{
                   
                    div{
                        padding: .12rem 0 ;
                    }
                    .ir1{
                        font-size: .22rem;
                        color: gray;
                    }
                    .ir3{
                        color: orange;
                    }
                }
            }
        }
    }
}
.button{
    width: 100%;
    height: 1.1rem;
    position: fixed;
    left: 0;
    bottom:0;
    background: #3d3d3f;
    display: flex;
    z-index:99999;
    .van-button{
        width: 70%;
        height: 100%;
        background: #3d3d3f;
        border:none;
    }
    .div1{
        width: 100%;
        height: 100%;
        text-align: center;
        div{
            height: 50%;
            line-height: .55rem;
            color: white;
            &:nth-of-type(1){
                font-size: .4rem;
            }
            &:nth-of-type(2){
                font-size: .22rem;
            }
        }
    }
    .div3{
        width: 30%;
        height: 100%;
        color: white;
        background: #535356;
        float: right;
        text-align: center;
        font-weight: bold;
        line-height: 1.1rem;
    }
    .div-a{
        width: 20%;
        height: 1.5rem;
        border-radius: 50%;
        position: fixed;
        left: .5rem;
        bottom: .1rem;
        background: #3190e8;
        text-align: center;
        span{
            display: inline-block;
        }
        i{
            font-size: 40px;
            color:white;
            margin-top: .35rem;
        }
    }
}
.add{
    height: .5rem;
    margin-top: 1.7rem
}
.add1{
    background: #3190e8;
    color: white;
    border: none;
    border-radius: 50%;
}
.ding{
    height: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgb(176, 223, 215);
    
    div{
        &:nth-of-type(1){
            font-size: .36rem;
            margin-left: .2rem
        }
        &:nth-of-type(2){
            font-size: .3rem;
            margin-right: .2rem
        }
    }
}
.watch{
    height: 1.2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    span{
        &:nth-of-type(1){
            display: inline-block;
            width: 2rem;
            text-align: center;
            font-size: .42rem;
            font-weight: bold;
        }
        &:nth-of-type(2){
            display: inline-block;
            width: .6rem;
            text-align: center;
            color: orange;
        }
        &:nth-of-type(3){
            display: inline-block;
            input{
                &:nth-of-type(1){
                    width: .42rem;
                    height: .42rem;
                    border: 1px solid #3190e8;
                    background: white;
                    color: #3190e8;
                    border-radius: 50%;
                    text-align: center;
                    line-height: .42rem;
                }
                &:nth-of-type(2){
                    width: .42rem;
                    height: .42rem;
                    border: none;
                }
                &:nth-of-type(3){
                    width: .42rem;
                    height: .42rem;
                    background: #3190e8;
                    color: white;
                    border-radius: 50%;
                    text-align: center;
                    line-height: .42rem;
                    border: none;
                }
            }
        }
    }
}
</style>       


